<template>
  <div class="box">
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="活动名称">
        <el-input v-model="name"></el-input>
      </el-form-item>
      <el-form-item label="活动区域">
        <el-select v-model="region" placeholder="请选择活动区域">
          <el-option label="上海" value="上海"></el-option>
          <el-option label="北京" value="北京"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="活动时间">
        <el-col :span="11">
          <el-date-picker
            type="date"
            placeholder="选择日期"
            v-model="date1"
            style="width: 100%"
          ></el-date-picker>
        </el-col>
        <el-col class="line" :span="2">-</el-col>
        <el-col :span="11">
          <el-time-picker
            placeholder="选择时间"
            v-model="date2"
            style="width: 100%"
          ></el-time-picker>
        </el-col>
      </el-form-item>
      <el-form-item label="即时配送">
        <el-switch v-model="delivery"></el-switch>
      </el-form-item>
      <el-form-item label="活动性质">
        <el-checkbox-group v-model="type">
          <el-checkbox label="美食/餐厅线上活动" name="type"></el-checkbox>
          <el-checkbox label="地推活动" name="type"></el-checkbox>
          <el-checkbox label="线下主题活动" name="type"></el-checkbox>
          <el-checkbox label="单纯品牌曝光" name="type"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="特殊资源">
        <el-radio-group v-model="resource">
          <el-radio label="线上品牌商赞助"></el-radio>
          <el-radio label="线下场地免费"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="活动形式">
        <el-input type="textarea" v-model="desc"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">立即创建</el-button>
        <el-button>取消</el-button>
      </el-form-item>
    </el-form>
    <el-drawer title="我是标题" :visible.sync="drawer" :direction="direction">
      <p>活动名称：{{ Myname }}</p>
      <p>活动区域：{{ Myregion }}</p>
      <p>活动时间：{{ Mydate1 }}{{ Mydate2 }}</p>
      <p>是否配送：{{ Mydelivery ? "是" : "否" }}</p>
      <p>活动性质：{{ Mytype }}</p>
      <p>特殊资源：{{ Myresource }}</p>
      <p>活动形式：{{ Mydesc }}</p>
    </el-drawer>
  </div>
</template>

<script>
export default {
  data() {
    return {
      name: "",
      region: "",
      date1: "",
      date2: "",
      delivery: false,
      type: [],
      resource: "",
      desc: "",
      drawer: false,
      direction: "rtl",
      Myname: "",
      Myregion: "",
      Mydate1: "",
      Mydate2: "",
      Mydelivery: false,
      Mytype: [],
      Myresource: "",
      Mydesc: "",
      date3: "",
      date4: "",
    };
  },
  methods: {
    onSubmit() {
      const selectedDate = new Date(this.date1); // 将日期格式化为 YYYY-MM-DD

      const formattedDate = selectedDate.toISOString().split("T")[0]; // 更新 form 中的日期属性

      this.date3 = formattedDate;

      const selectedDateTime = new Date(this.date2); // 获取小时、分钟和秒

      const hours = selectedDateTime.getHours().toString().padStart(2, "0");
      const minutes = selectedDateTime.getMinutes().toString().padStart(2, "0");
      const seconds = selectedDateTime.getSeconds().toString().padStart(2, "0"); // 将时间格式化为 HH:mm:ss

      const formattedTime = `${hours}:${minutes}:${seconds}`; // 更新 form 中的日期时间属性

      this.date4 = formattedTime;
      console.log(this.date4, this.date3);
      this.drawer = true;
      (this.Myname = this.name),
        (this.Myregion = this.region),
        (this.Mydate1 = this.date3),
        (this.Mydate2 = this.date4),
        (this.Mydelivery = this.delivery),
        (this.Mytype = String(this.type)),
        (this.Myresource = this.resource),
        (this.Mydesc = this.desc);
      console.log(this.type);
    },
  },
};
</script>

<style scoped>
.box {
  width: 400px;
  margin: 30px auto;
}
</style>
